.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: bold;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 7px solid midnightblue;
    animation: resize 2s infinite linear;
}

.loader__wrapper {
    display: flex;
    justify-content: center;
    margin-top: 40px;
}

@keyframes resize {
    from {
        transform: scale(1);
    }
    to {
        transform: scale(2);
    }
}